.shoeProductCards {
	width: 100%;
	padding: 0px 7rem;
	display: grid;
	justify-content: space-around;
	flex-wrap: wrap;
	margin-top: 2rem;
	grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

.box-1,
.box-2,
.box-3 {
	height: 370px;
	background-size: cover;
	margin: 0px 50px;
	border-radius: 10px;
	position: relative;
}
.box-1 {
	background-image: url('../../Images/box-1.png');
}
.box-2 {
	background-image: url('../../Images/box-2.png');
}
.box-3 {
	background-image: url('../../Images/box-3.png');
}
.product-container {
	display: flex;
	justify-content: center;
	margin: 0px 0px 2rem 0px;
	flex-wrap: wrap;
}
.price {
	font-family: arial;
	position: absolute;
	font-size: 2rem;
	color: #ffffff;
	font-style: oblique;
	font-weight: 400;
	text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
	right: 7%;
	top: 16%;
	transform: translate(-6%, -16%);
}
.icon {
	width: 40px;
}
.type-heading {
	font-family: calibri;
	font-weight: bold;
	color: #191919;
	padding: 0px 0px 1.5rem 0px;
}
.detail {
	position: absolute;
	left: 30px;
	bottom: 2rem;
}
.type-detail p {
	font-family: arial;
	font-weight: 400;
	color: #3a3a3a;
	letter-spacing: 1px;
	line-height: 5px;
	font-size: 1.8rem;
}
.product-img img {
	width: 23rem;
	transform: rotate(52deg);
}
.product-img {
	display: flex;
	justify-content: center;
	position: relative;
	align-items: center;
	height: 70%;
}
.cart-btn {
	position: absolute;
	right: 0px;
	bottom: 0px;
	width: 5rem;
	height: 4rem;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #ffffff;
	box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.1);
	border-bottom-right-radius: 10px;
}
.cart-btn a {
	color: #414040;
	font-size: 2.4rem;
	font-family: calibri;
	font-weight: bold;
}
.cart-btn a:hover {
	color: #1d1d1d;
	transition: all ease 0.2s;
}

@media only screen and (max-width: 1150px) {
	.shoeProductCards {
		padding: 0rem 2rem 6rem 2rem;
	}

	.box-1,
	.box-2,
	.box-3 {
		height: 370px;
		margin: 0px 40px;
	}
}

@media only screen and (max-width: 900px) {
	.shoeProductCards {
		padding: 0px;
		padding-bottom: 6rem;
	}

	.box-1,
	.box-2,
	.box-3 {
		height: 330px;
		margin: 0px 16px;

		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}
}

@media only screen and (max-width: 600px) {
	.shoeProductCards {
		padding: 0px 4rem;
		padding-bottom: 6rem;
	}

	.box-1,
	.box-2,
	.box-3 {
		height: 320px;
		margin: 0px 10px;
	}

	.product-img img {
		width: 28rem;
		transform: rotate(52deg);
	}
	.product-img {
		display: flex;
		justify-content: center;
		position: relative;
		align-items: center;
		height: 84%;
	}
}

@media only screen and (max-width: 515px) {
	.price {
		right: 16%;
		top: 51%;
	}
	.product-img img {
		width: 30rem;
		transform: rotate(52deg);
	}
	.product-img {
		display: flex;
		justify-content: center;
		position: relative;
		align-items: center;
		height: 92%;
	}
}

@media only screen and (max-width: 400px) {
	.shoeProductCards {
		padding: 0px 6rem;
		padding-bottom: 6rem;
	}

	.box-1,
	.box-2,
	.box-3 {
		height: 320px;
		margin: 0px 10px;
	}

	.product-img img {
		width: 27rem;
		transform: rotate(52deg);
	}
	.product-img {
		display: flex;
		justify-content: center;
		position: relative;
		align-items: center;
		height: 92%;
	}
}
