.productDetails {
	padding: 7rem 2rem 2.5rem;

	&__row {
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap;
		justify-content: space-around;
		padding: 1.5rem 2rem;
		margin-bottom: 4rem;
	}

	& select {
		max-width: 150px;
		font-size: 1.5rem;
	}

	&__cartCard {
		padding: 0px;
	}

	&__relatedProducts {
		display: grid;
		margin-top: 2rem;
		grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
	}

	& textarea.form-control {
		height: auto;
		font-size: 1.5rem;
	}
}

.col-2 {
	//flex-basis: 50%;
	width: 100%;
	min-width: 300px;
}

.col-2 img {
	max-width: 100%;
	padding: 20px 0;
}

.col-2 h1 {
	font-size: 50px;
	line-height: 60px;
	margin: 25px 0;
}

.header {
	background: radial-gradient(#ffffff, #faadbd);
}

.header .row {
	margin-top: 70px;
}

.categories {
	margin: 70px 0;
}

.col-3 {
	flex-basis: 30%;
	min-width: 250px;
	margin-bottom: 30px;
}

.col-3 img {
	width: 100%;
}

.small-container {
	max-width: 1080px;
	margin: auto;
	padding-left: 25px;
	padding-right: 25px;
}

.col-4 {
	flex-basis: 25%;
	padding: 10px;
	min-width: 200px;
	margin-bottom: 50px;
	transition: transform 0.5s;
}

small {
	color: #555555;
}

.row-2 {
	justify-content: space-between;
	margin: 100px auto 50px;
}

select {
	border: 1px solid coral;
	padding: 5px;
}

select:focus {
	outline: none;
}

.page-btn {
	margin: 0 auto 80px;
}

.page-btn span {
	display: inline-block;
	border: 1px solid #faadbd;
	margin-left: 10px;
	width: 40px;
	height: 40px;
	text-align: center;
	line-height: 40px;
	cursor: pointer;
}
.page-btn span:hover {
	background: #f1607d;
	color: whitesmoke;
}

.single-product {
	display: flex;
	flex-direction: row;

	&__imgwrapper {
		flex: 0.8;
	}
}

.single-product .col-2 img {
	padding: 0;
}

.single-product .col-2 {
	padding: 20px;
}

.single-product h4 {
	margin: 20px 0;
	font-size: 2rem;
	font-weight: bold;
}

.single-product select {
	display: block;
	padding: 10px;
	margin-top: 20px;
}

.single-product input {
	width: 50px;
	height: 40px;
	padding-left: 10px;
	font-size: 20px;
	margin-right: 10px;
	border: 1px solid coral;
}

input:focus {
	outline: none;
}

.single-product .fa {
	color: #f1607d;
	margin-left: 10px;
}

.small-img-row {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	flex: 0.2;
}

.small-img-col {
	flex-basis: 20%;
	cursor: pointer;
	padding: 10px;

	.image-active {
		border: 2px var(--pink) solid;
	}
}

.form {
	&__group {
		display: flex;
	}
	&__radio-group {
		display: inline-block;

		@include respond(tab-port) {
			width: 100%;
		}
	}

	&__radio-input {
		display: none;
	}

	&__radio-label {
		font-size: 1.6rem;
		cursor: pointer;
		position: relative;
		padding-left: 4.5rem;
	}

	&__radio-button {
		height: 3rem;
		width: 3rem;
		border: 5px solid red;
		border-radius: 50%;
		display: inline-block;
		position: absolute;
		left: 0;
		top: -0.4rem;

		&::after {
			content: '';
			display: block;
			height: 1.3rem;
			width: 1.3rem;
			border-radius: 50%;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			background-color: green;
			opacity: 0;
			transition: opacity 0.2s;
		}
	}

	&__radio-input:checked ~ &__radio-label &__radio-button::after {
		opacity: 1;
	}
}

@media only screen and (max-width: 900px) {
	.productDetails {
		&__relatedProducts {
			margin-top: 2rem;
			grid-template-columns: repeat(auto-fit, minmax(145px, 1fr));
		}
	}
}

@media only screen and (max-width: 600px) {
	.productDetails {
		padding: 11rem 1rem 2rem;
		overflow-x: hidden;

		&__cartCard {
			padding: 0px 15px;
		}

		&__relatedProducts {
			margin-top: 2rem;
			grid-template-columns: repeat(auto-fit, minmax(135px, 1fr));
		}
	}

	// .card{

	// }
	.small-container {
		padding: 0;
	}
	.row {
		text-align: center;
	}
	.col-2,
	.col-3,
	.col-4 {
		flex-basis: 100%;
	}

	.single-product .row {
		text-align: left;
	}

	.single-product .col-2 {
		padding: 20px 0;
	}

	.single-product h1 {
		font-size: 26px;
		line-height: 32px;
	}

	.cart-info p {
		display: none;
	}
}
