:root {
	--level2-height: 170px;
	--level2-width: 110px;
	--level1-height: 200px;
	--level1-width: 130px;
	--level0-height: 270px;
	--level0-width: 160px;

	--level-2-left: 650px;
	--level-1-left: 500px;
	--level0-left: 325px;
	--level1-left: 180px;
	--level2-left: 50px;
}

.section__trending {
	& #carousel {
		width: 810px;
		margin: auto;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
	}

	& .arrow {
		position: absolute;
		width: 30px;
		height: 30px;
		background-color: white;
		text-align: center;
		border-radius: 50%;
		cursor: pointer;
		font-size: 35px;
		color: #228291;
		line-height: 30px;
		margin-top: 85px;
		z-index: 10;
	}

	.arrow-right {
		left: 780px;
	}

	.item {
		text-align: center;
		color: white;
		font-size: 40px;
		position: absolute;
		transition: height 1s, width 1s, left 1s, margin-top 1s, line-height 1s,
			background-color 1s;
	}

	.level-2 {
		height: var(--level2-height);
		width: var(--level2-width);
		line-height: var(--level2-height);
		background-color: #228291;
		left: var(--level-2-left);
		margin-top: 25px;
	}

	.level-1 {
		height: var(--level1-height);
		width: var(--level1-width);
		line-height: var(--level1-height);
		background-color: #6796e5;
		left: var(--level-1-left);
		margin-top: 10px;
	}

	.level0 {
		height: var(--level0-height);
		width: var(--level0-width);
		line-height: var(--level0-height);
		background-color: #4ec9e1;
		left: var(--level0-left);
	}

	.level1 {
		height: var(--level1-height);
		width: var(--level1-width);
		line-height: var(--level1-height);
		background-color: #6796e5;
		margin-top: 10px;
		left: var(--level1-left);
	}

	.level2 {
		height: var(--level2-height);
		width: var(--level2-width);
		line-height: var(--level2-height);
		background-color: #228291;
		margin-top: 25px;
		left: var(--level2-left);
	}

	.left-enter {
		opacity: 0;
		left: var(--level2-left) - var(--level2-width);
		height: var(--level2-height) - 30;
		width: var(--level2-width) - 20;
		line-height: var(--level2-height) - 30;
		margin-top: 40px;
	}

	.left-enter.left-enter-active {
		opacity: 1;
		z-index: 100;
		left: var(--level2-left);
		height: var(--level2-height);
		width: var(--level2-width);
		line-height: var(--level2-height);
		margin-top: 25px;
		transition: left 1s, opacity 1s, height 1s, width 1s, margin-top 1s,
			line-height 1s;
	}

